<template>
    <div class="wave-chart">
        <span class='selectUnit' @click='setUnit' :title='title.selectUnit'></span>
        <span @click='maxOrMin' class='max' :title='maxData.title'></span>
        <span v-if='otp === 1' class='exportMusic' :title='title.exportMusic'></span>
        <span v-if='otp !== 3' class='exportData' :title='title.exportData'></span>
        <span class='restore' :title='title.restore'></span>
        <span class='dataZoom' :title='title.enlarge'></span>
        <span class='exportImage' :title='title.saveImage'></span>
        <span v-if='otp === 1' class='subTime' :title='title.subTime' @click="subTime"></span>
        <!--<span v-if='otp === 2 && factoryType != 1' class='setAllPower' :title='title.setAllPower' @click='setPower'></span>-->

    </div >
</template>

<script>
    import myChart from 'assets/lib/waveChart/myCharts.js'
    export default {
        name: "WaveChart",
        props: ['option', 'otp', 'height', 'type', 'change', 'position'],
        data: function() {
            return {
                /*waveChart: {}*/
                chart: null,
                title: {
                    selectUnit: '',
                    subTime: '',
                    exportMusic: '',
                    exportData: '',
                    restore: '',
                    saveImage: '',
                    enlarge: '',
                    setAllPower: '',
                    showGate: '',
                },
                maxData: {
                    title: '',
                    flag: 1,
                },
                subTimeFlag: 0,
                factoryType: 0, // 0314
            }
        },
        mounted: function() {
            this.chart = myChart.init(this.$el);
            this.chart.setOption(this.option);
        },
        watch: {
            //option
            option: {
                handler() {
                    this.chart.setOption(this.option);
                    if (this.option.x.data.length > 0 && this.option.y.data.length > 0 && this.otp == 2) {
                        $('.loadWave').css('display', 'none');
                    }
                },
                deep: true //
            },
            type: function() {
                this.title.subTime = this.type === 2 ? '' : '';
            },
            change: function() {
                if (this.change) {
                    document.getElementsByClassName('subTime')[0].classList.remove('active');
                    this.subTimeFlag = 0;
                }
            }
        },
        methods: {
            maxOrMin: function(e) {
                let thisWave = e.currentTarget.parentNode;
                let waveWidth = (window.innerWidth - 350)/2 + 'px';
                let wave = document.getElementsByClassName('wave-chart');
                if (this.maxData.flag) {
                    this.maxData.title = '';
                    e.currentTarget.classList.add('back');
                    thisWave.style.height = this.height.maxHeight;
                    thisWave.style.width = '100%';
                    switch (this.otp) {
                        case 1:
                            wave[1].style.display = 'none';
                            wave[2].style.display = 'none';
                            break;
                        case 2:
                            wave[0].style.display = 'none';
                            wave[2].style.display = 'none';
                            break;
                        case 3:
                            wave[0].style.display = 'none';
                            wave[1].style.display = 'none';
                            break;
                    }
                } else {
                    this.maxData.title = '';
                    e.currentTarget.classList.remove('back');
                    if (this.otp === 3) {
                        thisWave.style.height = this.height.historyHeight;
                        thisWave.style.width = '100%';
                    } else {
                        thisWave.style.height = this.height.waveHeight;
                        thisWave.style.width = waveWidth;
                    }
                    for (let i = 0, l = wave.length; i < l; i++) {
                        wave[i].style.display = 'block';
                    }
                }
                this.$emit('get-max', this.maxData.flag);
                this.maxData.flag = Math.abs(--this.maxData.flag);
            },
            setUnit: function() {
                let noUnitPosType = [200,1,4,9,12,15,16];
                let posType = Number(this.position.pos_type);
                if (this.factoryType != 3 && noUnitPosType.includes(posType)){
                    swal('');
                }
                else if(this.position.dgm_type==10 && this.position.pos_type==3){
                    swal('');
                }
                else{
                    this.$emit('set-unit', this.otp);
                }

            },
            subTime: function(e) {
                if (this.subTimeFlag === 1) {
                    e.currentTarget.classList.remove('active');
                    this.subTimeFlag = 0;
                } else {
                    this.subTimeFlag ?
                        e.currentTarget.classList.remove('active') :
                        e.currentTarget.classList.add('active');
                    this.subTimeFlag = Math.abs(--this.subTimeFlag);
                }
            },
            setPower: function() {
                this.$emit('set-power');
            },
        }
    }
</script>

