<template>
    <div id="wave-content">
        <header class="myHeader-data">
            <div class="form-group">
                <div class="select-group">
                    <span class="lableName"></span>
                    <treeselect class="select-header" :noOptionsText="noTips" v-model="chooseTid" :multiple="false" :options="options" :clearable="false" placeholder="" @select="currDeptChange"/>
