                        <option v-for="item in sortMacList" :value="item.mac_id" :title="item.mac_me">{{item.mac_me}}</option>
                    </select> -->
                </div>
                <div class="select-group">
                    <span class="lableName"></span>
                    <treeselect class="select-header" :noOptionsText="noTips" v-model="choosePosTypeId" :multiple="false" :options="reSetPosList" :clearable="false"  placeholder="" @select="dataPosChange"/>
                    <!-- <select id="select-position" class="select-header" v-model="choosePosTypeId" @change="selectPosTypeId">
                        <option v-for="item in choosePosList" :value="item.pos_type+'_'+item.pos_id" :title="item.pos_name" >{{item.pos_name}}</option>
                    </select> -->
                </div>
            </div>
            <div class="speed_choose_div" id="hd_choose">
                <select @change="change_hd()" class="hd_select" v-model="hd_speed">
                    <option value="false" id="hd_low_radio"></option>
                    <option value="true" id="hd_high_radio"></option>
                </select>
            </div>
            <button class="btn_samp" v-show="selectPosition.pos_type == 200" @click="showSampAlert"></button>
            <div class="header-time">
                <span></span>
                <el-date-picker
                        v-model="timeValue"
