                    <wave-chart :position="selectPosition" v-bind:otp="1" v-bind:option="waveOption" v-show="showType1 == 1" ref="chart1" @set-unit="showUnitSelect(1)" :height="maxRestoreHeight" :type="specType" @get-max="getMax" :change="changePos">
                    </wave-chart>
                    <!---->
                    <wave-chart :position="selectPosition" v-bind:otp="2" v-bind:option="specturmOption" style="float: right;" v-show="showType2 == 2" ref="chart2" @set-unit="showUnitSelect(2)" :height="maxRestoreHeight" @get-max="getMax" @set-power="setAllPower"></wave-chart>
                    <!---->
                    <wave-chart :position="selectPosition" v-bind:otp="3" v-bind:option="historyOption" v-show="showType3 == 3" ref="chart3" @set-unit="showUnitSelect(3)" :height="maxRestoreHeight" @get-max="getMax"></wave-chart>
