                    t_children[2].value = Number(itemList[2 * i - 1].getAttribute("bsf"));
                    t_children[2].speedRate = obj.speedRate;
                    t_children[3].value = 2 * Number(itemList[2 * i - 1].getAttribute("bsf"));
                    t_children[3].speedRate = obj.speedRate;
