            waveFFT: function(data, freq, nFirst, nCount, mi) {
                if(this.selectPosition.pos_type==14) {//2600,fft
                    return [[],[],[]];
                }
                var nPointNumber = 1;
                for (var i = 0; i < mi; i++)
                    nPointNumber *= 2;

                var pBegin = nFirst;
                var nWaveCount = data.length;
                if (nFirst >= nWaveCount)
                    return -1;
                if ((nFirst + nCount) > nWaveCount)
                    nCount = nWaveCount - nFirst;

                var all_count = nCount;
                var fDF = freq / nPointNumber;
                if (this.specType == 2) {
                    fDF = fDF * this.spectral / this.maxFreq;
                }

                // 
                var x_arr = [];
                var y_arr = [];
                // x_arr.length = (nPointNumber + 1);
                x_arr[0] = 0; // 
                for (i = 0; i < (nPointNumber - 1); i++) {
                    // i * nCount int
                    // double
                    var pos = i;
                    pos = parseInt((pos * (nCount - 1)) / nPointNumber);
                    var ipos0 = pos;
                    var ipos1 = ipos0 + 1;
                    var v0 = data[pBegin + ipos0];
                    // // console.log(pBegin + ipos0);
                    var v1 = data[pBegin + ipos1];
                    // 
                    var t = pos - ipos0;
                    var v = v0 + (v1 - v0) * t;
                    x_arr.push(v);
                }
                x_arr.push(data[pBegin + nCount - 1]); // 

                //			if (x_arr.length != (nPointNumber+1))
                //				return -1;

                // averagex[i] -= average
                var sum = 0;
                for (i = 1; i < x_arr.length; i++)
                    sum += x_arr[i];
                var average = sum / nPointNumber;
                for (i = 1; i < x_arr.length; i++)
                    x_arr[i] -= average;

                // y_arr.length = (nPointNumber + 1);
                this.offt(1, mi, x_arr, y_arr, nPointNumber);
                // 
                var t_fMaxFZ = 0;
                var number = parseInt(nPointNumber / 2.56);
                var OutX = [];
                var OutY = [];
                var OutPhase = [];
                var pAlarm = 1; //
                switch (this.alarm_type) {
                    case 0: //
                        pAlarm = Math.sqrt(2) / 2;
                        break;
                    case 2: //
                        pAlarm = 2
                        break;
                }
                // OutX.length = number;
                // OutY.length = number;
                // OutPhase.length = number;
                for (i = 0; i < number; i++) {
                    var xi = x_arr[i + 1];
                    var yi = y_arr[i + 1];
                    // 
                    // OutX.push(Math.round(i * fDF * 10000) / 10000);
                    OutX.push(Math.ceil(i * fDF * 100000) / 100000);
                    //  mantis 695 *4  *2
                    // OutY.push(Math.round(Math.sqrt(xi * xi + yi * yi) * 2 / nPointNumber * pAlarm * 1000000) / 1000000);
                     OutY.push(((Math.sqrt(xi ** 2 + yi ** 2) * 2) / nPointNumber) * pAlarm);
                    //  ()
                    OutPhase.push(Math.atan2(yi, xi));
                    // 
                    t_fMaxFZ = Math.max(OutY[i], t_fMaxFZ);
                }

                if (t_fMaxFZ < Number.NEGATIVE_INFINITY || t_fMaxFZ > Number.POSITIVE_INFINITY) {
                    t_fMaxFZ = -1;
                    this.fMaxFZ = t_fMaxFZ;
                    return t_fMaxFZ;
                }
                this.fMaxFZ = t_fMaxFZ;
                return [OutX, OutY, OutPhase];
            },
            offt: function(dir, i_dianshu_mi, x, y, i_dianshu) {
                i_dianshu = i_dianshu || 0;
                var i, j, k, l, m, l1;
                var t1, t2, u1, u2, w1, w2, p2, z;

                // Calculate the number of points
                if (i_dianshu == 0) {
                    i_dianshu = 1;
                    for (i = 0; i < i_dianshu_mi; i++)
                        i_dianshu *= 2;
                }

                // y.length = i_dianshu + 1;
                for (i = 0; i <= i_dianshu; i++)
                    y[i] = 0;

                j = 1;

                for (l = 1; l <= (i_dianshu - 1); l++) {
                    if (l < j) {
                        t1 = x[j];
                        //					t2 = y[j];
                        x[j] = x[l];
                        //					y[j] = y[l];
                        x[l] = t1;
                        //					y[l] = t2;
                    }
                    k = (i_dianshu) >> 1;
                    while (k < j) {
                        j -= k;
                        k = k >> 1;
                    }
                    j = j + k;
                }
                m = 1;

                for (i = 1; i <= i_dianshu_mi; i++) {
                    u1 = 1;
                    u2 = 0;
                    k = m;
                    m = m << 1;
                    p2 = Math.PI / k;
                    w1 = (Math.cos(p2));
                    //				w2 = (-Math.sin(p2));
                    //				w2 = -w2;
                    w2 = (Math.sin(p2));
                    for (j = 1; j <= k; j++) {
                        for (l = j; l <= i_dianshu; l += m) {
                            l1 = l + k;
                            t1 = x[l1] * u1 - y[l1] * u2;
                            t2 = x[l1] * u2 + y[l1] * u1;
                            x[l1] = x[l] - t1;
                            y[l1] = y[l] - t2;
                            x[l] += t1;
                            y[l] += t2;
                        }
                        z = u1 * w1 - u2 * w2;
                        u2 = u1 * w2 + u2 * w1;
                        u1 = z;
                    }
                }
            },
            makeFftData: function(type, arrayY) { //
                /**arrayY=**/
                var t_sourceSpectrumY = [];
                if (type == 1) { //
                    t_sourceSpectrumY = arrayY.concat();
                } else {
                    t_sourceSpectrumY = this.scoreSpectrumY.concat();
                }
                var i = 0;
                var len = t_sourceSpectrumY.length;
                var tmpSpectrumY = [];
                var tmpSpectrumX = [];
                var t_hideLow = this.hide_low;
                var a_index = 0;
                var b_index = 0;
                /****/
                for (i = 0; i < len; i++) {
                    if (t_hideLow > 0 && this.sourceSpectrumX[i] <= t_hideLow) {
                        tmpSpectrumY.push(0);
                    } else {
                        tmpSpectrumY.push(t_sourceSpectrumY[i]);
                    }
                }
                /****/
                if (this.power_a == "NaN") {
                    this.power_a = 0;
                }
                if (Number(this.power_a) < Number(this.power_b) || this.power_b == 0) {
                    this.isSetPower = true;
                    tmpSpectrumX = this.sourceSpectrumX.concat();
                    if (Number(this.power_a) < tmpSpectrumX[tmpSpectrumX.length - 1]) {
                        if (this.specType == 2) {
                            a_index = this.indexOfArray(tmpSpectrumX, this.power_a, 1);
                        } else {
                            a_index = this.indexOfArray(tmpSpectrumX, this.power_a, 1);
                        }
                        if (this.power_b == 0) {
                            b_index = tmpSpectrumX.length;
                        } else {
                            if (this.specType == 2) {
                                b_index = this.indexOfArray(tmpSpectrumX, this.power_b, -1) + 1;
                            } else {
                                b_index = this.indexOfArray(tmpSpectrumX, this.power_b, -1) + 1;
                            }
                        }
                        if (this.flag_f_nx.val == 1) {
                            tmpSpectrumX = this.freqXtrum.slice(a_index, b_index);
                        } else {
                            tmpSpectrumX = tmpSpectrumX.slice(a_index, b_index);
                        }
                        tmpSpectrumY = tmpSpectrumY.slice(a_index, b_index);
                    } else {
                        if (this.flag_f_nx.val == 1) {
                            tmpSpectrumX = this.freqXtrum;
                        }
                        tmpSpectrumY = this.sourceSpectrumY;
                    }
                } else {

                }
                this.spectrumX = tmpSpectrumX;
                this.spectrumY = tmpSpectrumY;
                // this.labelLineSpec();
            },
            indexOfArray: function(arr, value, flag) { //
                /**flag=0flag=1flag=-1**/
                var index = -1;
                var flag_pm = false;
                var dx = Number.POSITIVE_INFINITY;
                for (var i = 0; i < arr.length; i++) {
                    switch (flag) {
                        case -1:
                            flag_pm = arr[i] - value <= 0 ? dx > Math.abs(arr[i] - value) : false;
                            break;
                        case 0:
                            flag_pm = dx > Math.abs(arr[i] - value);
                            break;
                        case 1:
                            flag_pm = arr[i] - value >= 0 ? dx > Math.abs(arr[i] - value) : false;
                            break;
                    }
                    if (flag_pm) {
                        dx = Math.abs(arr[i] - value);
                        index = i;
                        if (dx == 0) {
                            break;
                        }
                    }
                }
                return index;
            },
