            getPeakIndexArr: function(yArr, count) { //
                /*yArr=y count=*/
                if (yArr.length <= 1) {
                    return [0];
                }
                var indexArr = [],
                    xVal = [];
                var t_yArr = []; //y
                var i = 0;
                var j = 0;
                var len = yArr.length;
                var obj = {};
                if (yArr[0] > yArr[1]) {
                    obj.key = 0;
                    obj.value = yArr[0];
                    t_yArr.push(obj);
                }
                for (i = 1; i < len - 1; i++) {
                    if (yArr[i] > yArr[i - 1] && yArr[i] > yArr[i + 1]) {
                        obj = {};
                        obj.key = i;
                        obj.value = yArr[i];
                        t_yArr.push(obj);
                    }
                }
                if (yArr[len - 2] < yArr[len - 1]) {
                    obj = {};
                    obj.key = len - 1;
                    obj.value = yArr[len - 1];
                    t_yArr.push(obj);
                }
                len = t_yArr.length;
                for (i = 0; i < count; i++) {
                    for (j = i + 1; j < len; j++) {
                        if (t_yArr[i].value < t_yArr[j].value) {
                            obj = t_yArr[i];
                            t_yArr[i] = t_yArr[j];
                            t_yArr[j] = obj;
                        }
                    }
                }
                if (count > len) {
                    count = len;
                }
                if (this.spectrumX.length !== this.cutSpectrum.x.length) {
                    for (i = 0; i < count; i++) {
                        xVal.push(this.cutSpectrum.x[t_yArr[i].key]);
                    }
                    for (i = 0, l = xVal.length; i < l; i++) {
                        indexArr.push(this.indexOfArray(this.spectrumX, xVal[i], 0))
                    }
                } else {
                    for (i = 0; i < count; i++) {
                        indexArr.push(t_yArr[i].key);
                    }
                }
                return indexArr;
            },
