            integralWave: function(t_waveY, samples, deltaT, dcCorr, t_sourceIndex) { //
                var i = 0;
                var result = [dcCorr];
                if (t_sourceIndex == 0) {
                    for (i = 0; i < samples; i++) {
                        t_waveY[i] = t_waveY[i] * 9.8;
                    }
                }
                for (i = 1; i < samples; i++) {
                    result[i] = result[i - 1] + (t_waveY[i] + t_waveY[i - 1]) * deltaT / 2;
                }
                return result;
            },
            waveDCOut: function(t_waveY, len) { //
                var i = 0;
                var avg = 0;
                // 
                for (i = 0; i < len; i++) {
                    avg += t_waveY[i] / len;
                }
                //
                for (i = 0; i < len; i++) {
                    t_waveY[i] = t_waveY[i] - avg;
                }
                return t_waveY;
            },
            integralFft: function(t_specY, len, deltaF, t_sourceIndex) { //
                var i = 0;
                var result = [0];
                // g9.8 
                var g = 1;
                if (t_sourceIndex == 0) {
                    g = 9.8;
                }
                for (i = 1; i < len; i++) {
                    result[i] = t_specY[i] * g * 1000 / (2 * Math.PI * deltaF * i);
                }
                return result;
            },
            labelLineWave: function() { //
                /****/
                if (this.nxArrayWave.length > 0) {
                    this.nxArrayWave = [];
                }
                var t_nxArray = [];
                var index = -1;
                var i = 0;
                var nx0 = -1; //
                var nx1 = -1; //
                var index1 = -1;
                var dx = -1; //
                /****/
                if ((this.flag_subTime == 1) && this.last_xWave != null && this.last_xWave.nx0 >= 0) {
                    nx0 = this.last_xWave.nx0;
                    index = this.indexOfArray(this.waveX, nx0, 0);
                    t_nxArray.push({
                        name: nx0 + "," + this.waveY[index].toFixed(4),
                        xAxis: index
                    });
                    this.flag_wave_extend = 1;
                } else if ((this.flag_subTime == 1) && this.last_xWave.nx0 == undefined) { //0
                    index = 0;
                    t_nxArray.push({
                        name: 0 + "," + this.waveY[index].toFixed(4),
                        xAxis: index
                    });
                    this.flag_wave_extend = 1;
                }
                /****/
                if (this.flag_wave_extend && (this.flag_subTime == 1) && this.last_xWave_time != null) {
                    nx1 = this.last_xWave_time.nx0;
                    if (nx1 != undefined) {
                        index1 = this.indexOfArray(this.waveX, nx1, 0);
                        dx = Math.abs(index1 - index);
                        if (dx == 0) {
                            return;
                        }
                        var x0 = index1 % dx;
                        for (var x = x0; x < this.waveX.length; x += dx) {
                            if (x == index) {
                                continue;
                            }
                            t_nxArray.push({
                                name: "",
                                xAxis: x,
                                lineStyle: {
                                    color: "red"
                                }
                            });
                        }
                    }
                }
                if (t_nxArray.length > 0) {
                    this.nxArrayWave = t_nxArray;
                }
            },
