            setAudioExport: function() { //
                var i = 0;
                var max = Number.MIN_VALUE;
                var frequency = this.maxFreq; //
                var pointSize = 16; //
                var channelNumber = 1; //
                var blockSize = channelNumber * pointSize / 8; //
                var wave = this.sourceWaveY.concat(); //
                var length = wave.length * pointSize / 8; //
                var buffer = new Uint8Array(length + 44); //wav
                var view = new DataView(buffer.buffer); //
                buffer.set(new Uint8Array([0x52, 0x49, 0x46, 0x46])); //"RIFF"
                view.setUint32(4, wave.length + 44, true); //
                buffer.set(new Uint8Array([0x57, 0x41, 0x56, 0x45]), 8); //"WAVE"
                buffer.set(new Uint8Array([0x66, 0x6D, 0x74, 0x20]), 12); //"fmt "
                view.setUint32(16, 16, true); //WAV
                view.setUint16(20, 1, true); //
                view.setUint16(22, 1, true); //
                view.setUint32(24, frequency, true); //
                view.setUint32(28, frequency * blockSize, true); //
                view.setUint16(32, blockSize, true); //
                view.setUint16(34, pointSize, true); //
                buffer.set(new Uint8Array([0x64, 0x61, 0x74, 0x61]), 36); //"data"
                view.setUint32(40, length, true); //
                for (i = 0; i < wave.length; i++) {
                    if (Math.abs(wave[i]) > max) {
                        max = Math.abs(wave[i]);
                    }
                }
                if (max == 0) { //0.001
                    max = 0.0001;
                }
                var xs = 32760 / max; //:short/maxshort(-32768~32767)
                for (i = 0; i < wave.length; i++) {
                    wave[i] = Math.round(wave[i] * xs); //
                }
                buffer.set(new Uint8Array(new Int16Array(wave).buffer), 44); //
                var fileName = this.fileName + "_.wav"; //
                this.funDownload(buffer, fileName);
            },
