            checkChange: function(flag, list, child, item) {
                var nextState = false; //
                var i = 0;
                var j = 0;
                var t_list = {};
                var t_child = {};
                var t_item = {};
                var flag_one_check = false; //
                switch (flag) {
                    case 0: //
                        nextState = !list.state;
                        if (list.children.length > 0) {
                            for (i = 0; i < list.children.length; i++) {
                                t_child = list.children[i];
                                t_child.state = nextState;
                                if (t_child.hasOwnProperty("children") && t_child.children.length > 0) {
                                    for (j = 0; j < t_child.children.length; j++) {
                                        t_item = t_child.children[j];
                                        t_item.state = nextState;
                                    }
                                }
                            }
                        }
                        break;
                    case 1: //
                        nextState = !child.state;
                        if (child.hasOwnProperty("children") && child.children.length > 0) {
                            for (j = 0; j < child.children.length; j++) {
                                t_item = child.children[j];
                                t_item.state = nextState;
                            }
                        }
                        if (nextState == false) {
                            for (i = 0; i < list.children.length; i++) {
                                t_child = list.children[i];
                                if (t_child.id == child.id) {
                                    continue;
                                }
                                if (t_child.state == true) {
                                    flag_one_check = true;
                                    break;
                                }
                            }
                            if (flag_one_check) {
                                list.state = true;
                            } else {
                                list.state = false;
                            }
                        } else {
                            list.state = true;
                        }
                        break;
                    case 2: //
                        nextState = !item.state;
                        if (nextState == false) {
                            flag_one_check = false;
                            for (j = 0; j < child.children.length; j++) {
                                t_item = child.children[j];
                                if (t_item.id == item.id) {
                                    continue;
                                }
                                if (t_item.state == true) {
                                    flag_one_check = true;
                                    break;
                                }
                            }
                            if (flag_one_check) {
                                child.state = true;
                            } else {
                                child.state = false;
                            }
                        } else {
                            child.state = true;
                        }
                        if (child.state == false) {
                            flag_one_check = false;
                            for (i = 0; i < list.children.length; i++) {
                                t_child = list.children[i];
                                if (t_child.id == child.id) {
                                    continue;
                                }
                                if (t_child.state == true) {
                                    flag_one_check = true;
                                    break;
                                }
                            }
                            if (flag_one_check) {
                                list.state = true;
                            } else {
                                list.state = false;
                            }
                        } else {
                            list.state = true;
                        }
                        break;
                };
                this.$nextTick(function() {
                    this.checkedNum();
                });
            },
