                        flag_checked = false;
                        for (l = 0; l < t_child_num.children.length; l++) {
                            t_item_num = t_child_num.children[l];
                            if (t_item_num.state == false) {
                                continue;
                            }
                            flag_checked = true;
                            t_item_num.label = t_num;
                        }
                        if (flag_checked) {
                            t_num++;
                        }
                    }
                }
                if (this.speedList[0].state == true && this.speedList[0].children.length > 0) {
                    t_num = 0;
                    for (l = 0; l < this.speedList[0].children.length; l++) {
                        t_item_num = this.speedList[0].children[l];
                        if (t_item_num.state == false) {
                            continue;
                        }
                        t_num++;
                        t_item_num.label = t_num;
                    }
                }
                this.failureOk();
            },
            checkAll: function(number, list) {
                var nextState = !list.state; //
                var i = 0;
                var j = 0;
                var child = {};
                var item = {};
                if (list.children.length > 0) {
                    for (i = 0; i < list.children.length; i++) {
                        child = list.children[i];
                        child.state = nextState;
                        if (child.hasOwnProperty("children") && child.children.length > 0) {
                            for (j = 0; j < child.children.length; j++) {
                                item = child.children[j];
                                item.state = nextState;
                            }
                        }
                    }
                }
            },
            chooseSpeed: function() { //
                if ($('#chooseYes').is(':checked')) { //
                    $('#failure_speed_number').attr('disabled', false);
                } else {
                    $('#failure_speed_number').attr('disabled', true);
                }
            },
            labelChanges: function(item) {
                if (this.selectPosition.position_type == 8 || this.selectPosition.position_type == 9) {
                    this.failureUnit = "NX"; //
                    this.failureUnit_detail = "NX"
                } else if (this.flag_f_nx.val) {
                    this.failureUnit_detail = "NX"
                    this.failureUnit = 'NX';
                } else {
                    this.failureUnit = "Hz";
                    this.name_type = "";
                    this.failureUnit_detail = "Hz"
                }

                var freq = 0;
                if (this.specType == 2) {
                    var t_curSpeedRate = 1;
                    if (this.isPosVibSeries(this.selectPosition.position_type)) {
                        t_curSpeedRate = this.selectPosition.speedRate;
                    }
                    freq = (item.value * item.speedRate / t_curSpeedRate).toFixed(2);
                } else {
                    freq = (item.value * item.speedRate * this.freq_speed_failure / 60).toFixed(2);
                    this.flag_f_nx.val && (freq = (freq * 60 / this.rotateSpeed).toFixed(2));
                }
                var types = "(" + item.type + "." + item.label + ")";
                var labelValue = types + freq + this.failureUnit;
                return labelValue;
            },
            addActive: function(event, index) {
                var target = event.currentTarget;
                /****/
                if (index == 0 || index == 1 || index == 6 || index == 10) {
                    $(target).toggleClass('active');
                }
                if (index == 9) { //
                    $(target).addClass('active');
                    if (this.autoPeakNum == -1) {
                        $(target).removeClass("active");
                    }
                }
                if (index == 2 && this.rotateSpeed == 0) { //
                    swal({
                        title: "",
                        text: '0x',
                        button: "",
                    });
                } else if (index == 2 && this.rotateSpeed != 0 && (!this.posOrderList.includes(Number(this.selectPosition.pos_type)))) {
                    $(target).toggleClass('active');
                }
                if (index == 3 && this.rotateSpeed == 0) { //1
                    swal({
                        title: "",
                        text: '01',
                        button: "",
                    });
                }
            },
           //2600
            setBaseWave(baseInfo,type){
                type=Number(type);//type==1:23
                let params = null;
                params = {
                    type: type,
                    dgm_id: this.selectPosition.dgm_id,
                    ch_id: this.selectPosition.ch_id,
                    time: this.oriChangeTime,
                    pos_name: this.selectPosition.pos_name,
                    pos_id: this.selectPosition.pos_id,
                    mac_id: this.chooseMacId,
                };
                switch(type){
                    case 3:
                        params.temperature = baseInfo.temperature;
                        params.wave_x = baseInfo.wave_x;
                        params.wave_y = this.waveY;
                        params.spaceTime = baseInfo.spaceTime;
                        params.fGain = baseInfo.fGain;
                        params.fStart = baseInfo.fStart;
                        params.fWidth = baseInfo.fWidth;
                        params.fThreshold = baseInfo.fThreshold;
                        break;
                    case 1:
                        params.temperature = baseInfo.temperature;
                        params.wave_x = baseInfo.wave_x;
                        params.wave_y = this.waveY;
                        break;
                }
                if(type!==2){
                    params.time = 0;
                }
                this.$getApi.setBasicWave(params)
                    .then(res=>{
                        if(res.msg===0){
                            swal({
                                title: "",
                                text: '',
                                button: "",
                            });
                        }
                        else{
                            swal({
                                title: "",
                                text: '',
                                button: "",
                            });
                        }
                    })
                    .catch(err=>{
                        console.log('' ,err)
                    })
            },
            // 2600
            getBaseWave(){
                let params = null;
                params = {
                    dgm_id: this.selectPosition.dgm_id,
                    channel: this.selectPosition.ch_id
                };
                this.$getApi.clickToDgmData(params)
                    .then(res=>{
                        if(res.msg===0){
                            swal({
                                title: "",
                                text: '',
                                button: "",
                            });
                        }
                        else{
                            swal({
                                title: "",
                                text: '',
                                button: "",
                            });
                        }
                    })
            },
            // 2600
            importBaseWave(){
                document.getElementById('waveSelect_alert').style.display = 'block';//
            },
            waveSelectOk(dom){
                this.closeAlert(dom);
                document.getElementById('fileLocal').click();
            },
            //
            selectFile(event){//
                let that = this;
                let selectedFile = {};//
                let target = event.target;
                that.waveX = [];
                that.waveY = [];
                if(that.p_isIE() && !target.files) {
                    let filePath = target.value;
                    let fileSystem = new ActiveXObject("Scripting.FileSystemObject");
                    selectedFile = fileSystem.GetFile(filePath);
                } else {
                    selectedFile = target.files;
                }
                for(let i=0;i<selectedFile.length;i++){
                    let size = selectedFile[i].size/1024;
                    if(size>2000){
                        swal({
                            title: "",
                            text: '2M',
                            button: "",
                        })
                        target.value="";
                        return;
                    }
                    let fileName=selectedFile[i].name;
                    let suffixName = fileName.substring(fileName.lastIndexOf(".")+1).toLowerCase();
                    if(suffixName !="csv"){
                        swal({
                            title: "",
                            text: 'csv',
                            button: "",
                        })
                        target.value="";
                        return;
                    }
                    let reader = new FileReader();//,.
                    reader.readAsText(selectedFile[i]);//,URL
                    reader.onload = function (event) {
                        //,result,
                        let datas = event.target.result;
                        let tmpArr = datas.split("\n");
                        let len = tmpArr.length;
                        let tObj = [];
                        let value1;//key
                        var i=0;
                        let baseInfo = {};//
                        for(i=0; i<len; i++){
                            tObj = [];
                            tObj = tmpArr[i].split(",");
                            if(tObj.length<2){
                                continue;
                            }
                            value1 = tObj[0];
                            if(value1=='dataBegin'){
                                i++;
                                break;
                            }
                            baseInfo[value1] = tObj[1];
                        }
                        if(baseInfo["fileType"] != 101){
                            swal({
                                title: "",
                                text: '',
                                button: "",
                            });
                            return;
                        }
                        that.baseInfo = baseInfo;
                        let t_spaceTime = Number(baseInfo["spaceTime"]);
                        var _axisX=[];
                        var _axisY=[];
                        that.baseInfo.wave_x = [];
                        for(;i<len;i++){
                            tObj = [];
                            tObj = tmpArr[i].split(",");
                            if(tObj.length<2){
                                continue;
                            }
                            value1 = tObj[0];
                            if(value1 == 'dataEnd'){
                                i++;
                                break;
                            }
                            that.baseInfo.wave_x.push(Number(value1));
                            _axisX.push(Number(value1)*t_spaceTime/1000);
                            _axisY.push(tObj[1]);
                        }
                        that.flag_real = 1;//
                        that.setRealBtn();
                        // that.ifImportBaseWave = 1;//
                        that.waveX = _axisX;
                        that.waveY = _axisY;
                        that.noRealData = false;
                        that.unitXWave = "s";
                        that.unitWave = "KN";
                        target.value = '';
                    }
                }
            },
            /**
             *	
             */
            p_browser(){
                let userAgent = navigator.userAgent.toLowerCase();
                let t_browser = {
                    version: (userAgent.match( /.+(?:rv|it|ra|ie)[\/: ]([\d.]+)/ ) || [])[1],
                    safari: /webkit/.test( userAgent ),
                    opera: /opera/.test( userAgent ),
                    msie: /msie/.test( userAgent ) && !/opera/.test( userAgent ),
                    mozilla: /mozilla/.test( userAgent ) && !/(compatible|webkit)/.test( userAgent )
                };
                return t_browser;
            },
            /**
             * IE
             */
            p_isIE(){
                let t_browser = this.p_browser();
                return t_browser.msie;
            },
        }
    }
</script>

